import Vue from 'vue'
import Router from 'vue-router'
import Url from '@/components/Urls'
import SignUp from '@/components/Signup'
import LogIn  from '@/components/LogIn'

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Url
    },
    {
      path: '/signup',
      name: 'signup',
      component: SignUp
    },
    {
      path: '/login',
      name: 'login',
      component: LogIn
    },{
      path:'*',
      name:"fallback",
      component: Url
    }
  ]
})



