<template>
    <nav class = "navbar navbar-dark navbar-expand-sm bg-dark mb-4">
      <div class="container">
        <router-link to="/" class="navbar-brand">{{ title }}</router-link>
        <button
          class = "navbar-toggler"
          type = "button"
          data-toggle = "collapse"
          data-target = "#mobile-nav">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class = "collapse navbar-collapse" id="mobile-nav">
          <ul class = "navbar-nav ml-auto">
            <template v-if='User' >
              <li class = "nav-item compromise" >
                <a href="#" class="nav-link"> {{ User.name }}</a>
              </li>
              <li class = "nav-item compromise">
                <a href="/logout" class="nav-link" @click="removeUser"  > Log Out </a>
              </li>
            </template>
            <template v-else >
              <li class = "nav-item compromise" >
                <router-link to="/login" class="nav-link"> Log In</router-link>
              </li>
              <li class = "nav-item compromise">
                <router-link to="/signup" class="nav-link"> Sign Up</router-link>
              </li>
            </template>
          </ul>
        </div>
      </div>
    </nav>
</template>

<script>
  import  { mapGetters } from 'vuex'
  import GLOBAL from '@/global/global'
    export default {
        name: "navbar",
        computed:{
          ...mapGetters(['User'])
        },
        methods:{
          removeUser(){
            this.$cookies.remove("token", {path: '/'});
            this.$store.dispatch("removeUser");
            this.$router.push('/');
          }
        },
      data(){
          return {
            title : GLOBAL.title
          }
      }

    }
</script>

<style scoped>
  .compromise{
    min-width: fit-content;
    padding: 0 9px;
    margin: 1px auto;
  }

</style>



