<template>
  <div class="container">
    <div class="row">
      <div class="col-md-10 col-sm-12 col-lg-10 m-auto">
        <h1 class="display-4 text-center">>> {{ title }} <<</h1>
        <form class="mt-5">
          <div class="input-group mt-5" >
            <input type="text" class="form-control form-control-lg adjust"
                   ref="longUrlref"
                   placeholder="Long Url" aria-label="Long Url"
                   :class="addLongUrlClass"
                   aria-describedby="Long-url" v-model="longUrl">
            <div class="input-group-append" >
              <span @click="submit" class="btn btn-info input-group-text" id="Long-url"> Shrink </span>
            </div>
          </div>
          <div v-show="shortUrlState !== 4" class="input-group my-5">
            <div class="input-group-prepend">
              <span class="input-group-text" id="short-url">{{ url }}</span>
            </div>
            <input type="text" class="form-control form-control-lg adjust"
                   :class="addShortUrlClass "
                   aria-describedby="short-url"
                   v-model="shortUrl"
                   placeholder="Short Url (Optional)">
          </div>
        </form>
        <div v-show="shortUrlState === 4 " class="input-group mt-5" >
          <input type="text" class="form-control form-control-lg adjust"
                 :class="addShortUrlClass"
                 ref="text"
                 aria-describedby="short-url" v-bind:value="completeShortUrl" readonly>
          <div class="input-group-append" >
            <span class="btn btn-success input-group-text" @click="copyShortUrl" > Copy </span>
          </div>
        </div>
        <div v-show="Url && Url.length > 0 " class="table-hover table-condensed table-responsive">
          <table class="table">
            <thead>
              <tr >
                <th class="text-center">go2u.ml/</th>
                <th class="text-center"> -> </th>
                <th class="text-center"> Action </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="url in Url ">
                <td class="text-center" ><h6 style="cursor: pointer;" @click="goTo(url.longUrl)" >{{ url.shortUrl }}</h6></td>
                <td class="text-center" ><h6 style="cursor: pointer;" @click="goTo(url.longUrl)" >{{ url.longUrl }}</h6></td>
                <td ><span class="btn btn-danger" style="cursor: pointer;" @click="removeUrl(url.shortUrl)">Delete</span></td>
                <!--<td  ><span class="btn btn-success" style="cursor: copy; !important;" @click="copyText(url.shortUrl)" >Copy</span></td>-->
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import axios from 'axios';
  import GLOBAL from '@/global/global'
    export default {
      /*
        0 - Default
        1 - Loading
        2 - Right
        3 - Wrong
        4 - Submitted
       */
      name: "Urls",
      data(){
        return{
          longUrl:"",
          shortUrl:"",
          shortUrlState:0,
          longUrlState:0,
          title: GLOBAL.title,
          url: GLOBAL.url
        }
      },
      computed:{
        addShortUrlClass(){
          switch(this.shortUrlState){
            case 0 : return 'default-state';
            case 1 : return 'loading';
            case 2 : return 'right';
            case 4 : return 'right';
            default : return 'wrong';
          }

        },
        addLongUrlClass(){
          switch(this.longUrlState){
            case 0 : return 'default-state';
            case 1 : return 'loading';
            case 2 : return 'right';
            case 4 : return 'right';
            default : return 'wrong';
          }

        },
        completeShortUrl(){
          if(this.shortUrlState === 4) {
            return GLOBAL.url + '/' + this.shortUrl;
          }else{
            return "";
          }
        },
        ...mapGetters(['Url', 'User'])
      },
      methods: {
        goTo(url){
          window.location = url;
        },
        // copyText(shortUrl){
        //     console.log('Copy text fired');
        //     this.tempData = GLOBAL.url+'/'+shortUrl;
        //     setTimeout(()=>{
        //       this.$refs.greyWorm.select();
        //       document.execCommand('copy');
        //       window.getSelection().removeAllRanges();
        //       this.$store.dispatch('notification', {header: "success", body: "Copied to Clipboard"})
        //     }, 2000);
        //
        // },
        copyShortUrl(){
          this.$refs.text.select();
          document.execCommand('copy');
          window.getSelection().removeAllRanges();
          this.$store.dispatch('notification', {header: "success", body: "Copied to Clipboard"})
        },
        checkShortUrl(){
          if(!this.shortUrl || this.shortUrl.length === 0){
            this.shortUrlState = 0;
          }else if(this.shortUrl.length > 0 && this.shortUrlState !== 4){
            //call here
            this.shortUrlState = 1;
            axios.post(GLOBAL.domain+'/api/url/check', {shortUrl: this.shortUrl})
              .then(res=>{
                if(res.data.available){
                  this.shortUrlState = 2;
                }else{
                  this.shortUrlState = 3;
                }
              })
              .catch(err=>{
                this.shortUrlState = 3;
                this.$store.dispatch('notification', {header: "error", body:err.response.data.message});
              })
          }
        },
        submit(){
          if(this.shortUrlState === 2 || this.shortUrlState === 0){
            if(this.longUrl.length > 0) {
              this.longUrlState = 1;
              axios.post(GLOBAL.domain + "/api/url", {longUrl: this.longUrl, shortUrl: this.shortUrl})
                .then(res=>{
                  this.longUrlState = 4;
                  this.shortUrl = res.data.shortUrl;
                  this.shortUrlState = 4;
                  if(this.User){
                    this.$store.dispatch('addUrl', {longUrl: res.data.longUrl, shortUrl: res.data.shortUrl});
                  }
                })
                .catch(err=>{
                  console.log(err);
                  this.longUrlState = 3;
                  this.shortUrlState = 3;
                  this.$store.dispatch('notification', {header: "error",body: err.response.data.message})
                })
            }else{
              this.$store.dispatch('notification', {header: "notification", body: "There is Nothing to short"});
            }
          }else{
          }
        },
        removeUrl(shortUrl){
          axios.delete(GLOBAL.domain+'/api/url/',{data:{shortUrl : shortUrl}})
            .then(res=>{
              if(res.data.delete) {
                this.$store.dispatch('removeUrl', res.data.shortUrl);
                this.$store.dispatch('notification', {header:"success" ,body : "Deleted Url"});
                this.resetAll();
              }else{
                this.$store.dispatch('notification', {header:"notification" ,body : "Delete Failed"})
              }
            })
            .catch(err=>{
              this.$store.dispatch('notification', {header:"error" ,body : err.response.data.message|| "OOPS Something wrong happen" });
            })
        },
        resetAll(){
          this.longUrl = "";
          this.shortUrl = "";
        }
      },
      watch:{
        shortUrl(newValue, oldValue) {

          if(this.longUrlState === 4){
            this.shortUrlState = 4;
          }else if(this.shortUrlState === 4){
            this.longUrlState = 0;
            this.shortUrlState = 0;
          } else if(newValue.length === 0){
            this.shortUrlState = 0
          }else if(this.shortUrlState !== 4 && newValue.length > 0){
            this.debounceCheckShort()
          }
        },
        longUrl(newValue, oldValue) {
          if(this.longUrlState === 4){
            this.longUrlState = 0;
            this.shortUrlState = 0;
            this.shortUrl = "";
          }
          else if(newValue.length === 0){
            this.longUrlState = 0
          }
        },
      },
      created(){
        this.debounceCheckShort = _.debounce(this.checkShortUrl, 700);

      },
      mounted(){
        this.$refs.longUrlref.focus();
      }
    }
</script>

<style scoped>

</style>



