import Vuex  from 'vuex'
import Vue from 'vue'
import _ from 'lodash'
import axios from 'axios'
import DOMAIN from '@/global/global'
import GLOBAL from '@/global/global'

Vue.use(Vuex);


export const store = new Vuex.Store({
  strict: true,
  state:{
    user:undefined,
    url:undefined,
    notification:{
      header:undefined,
      body: undefined,
      footer: undefined,
      show: false
    }
  },
  getters:{
    User(state){
      return state.user;
    },
    Url(state){
      return state.url;
    },
    Notification(state){
      return state.notification;
    }
  },
  mutations:{
    addUser(state, payload){
      state.user = payload;
    },
    addUrl(state, payload)  {
      if(payload instanceof Array) {
        state.url = payload;
      }else if(payload instanceof Object){
        if(state.url){
          state.url.push(payload);
        }else{
          state.url = [payload];
        }
      }
    },
    removeUser(state){
      state.user = undefined;
    },
    removeUrls(state) {
      state.url = undefined;
    },
    removeUrl(state,payload){
      if(state.url && state.url instanceof Array) {
        for (let i = 0; i < state.url.length; i++) {
          if (state.url[i].shortUrl === payload) {
            state.url.splice(i, 1);
            break;
          }
        }
        if(state.url.length === 0){
          state.url = undefined;
        }
      }
    },
    addNotification(state, payload){
      state.notification.header = payload.header;
      state.notification.body = payload.body;
      state.notification.footer = payload.footer;
      state.notification.show = payload.show || false;
    }
  },
  actions:{
    addUser(context, payload){
      context.commit('addUser', payload);
      context.dispatch('urlList')
    },
    authenticateUser(context){
      axios.get(DOMAIN.domain+'/api/user/authenticateuser')
        .then(res=>{
          if(res.data.user){
            context.commit('addUser',
              {
                name:res.data.user.name,
                email: res.data.user.email,
                verified: res.data.user.verified
              });
            context.dispatch('urlList');
          }
          else{
            context.dispatch('removeUser');
            context.commit('removeUrls');
          }
        })
        .catch(err=>{
          console.log("Failed to authenticate");
          // context.dispatch('notification', {header: "warning", body: err, showTime:10000})
      });
    },
    urlList(context){
      console.log(context.getters.User);
      if(context.getters.User) {
        console.log('dispatch called');
        axios.get(GLOBAL.domain + '/api/url/urllist')
          .then(res => {
            context.commit('addUrl', res.data);
          })
          .catch(err => {
            console.log(err);
            context.dispatch('notification', {header: "error", body: "failed to fetch urls list"});
          })
      }
    },
    addUrl(context, payload){
      context.commit('addUrl',payload);
    },
    removeUser(context){
      context.commit('removeUser');
      context.commit('removeUrls');
    },
    removeUrl(context, payload) {
      context.commit('removeUrl', payload);
    },
    notification(context, payload){
      payload.showTime = payload.showTime || 1500;
      payload.show = true;
      context.commit('addNotification', payload);
      setTimeout(()=>{
        payload.show = false;
        context.commit('addNotification', payload);
      },payload.showTime );
    }
  },
});



