<template>
  <div class="container">
    <div class="row">
      <div class="col-md-8 col-sm-12 col-lg-6 m-auto">
        <h1 class="display-4 text-center">Log In</h1>
        <p class="lead text-center">Welcome Back</p>
        <form >
          <div class="form-group">
            <input ref="email" type="text" class="form-control form-control-lg adjust" :class="addEmailClass" placeholder="Email" v-model="email">
          </div>
          <div class="form-group">
            <input type="password" class="form-control form-control-lg " placeholder="Password" v-model="password">
          </div>
          <div class="form-group">
            <input type="submit" @click.prevent="submit" class="btn btn-info btn-block mt-4" placeholder="Log In">
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import axios from 'axios'
  import GLOBAL from '@/global/global'
    export default {
        name: "Login",
      data(){
          return{
            email:"",
            password:"",
            emailState: 0
          }
      },
      computed:{
        addEmailClass(){
          /*
          0 default
          1 loading
          2 right
          3 wrong
           */
          switch (this.emailState) {
            case 0 : return 'default-state';
            case 1 : return 'loading';
            case 2 : return 'right';
            default : return 'wrong';
          }
        },
        ...mapGetters(['User'])
      },
      methods:{
        validateEmail(email){
          let reg = /(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])/g
          return reg.test(email);
        },
        submit(){
          // console.log('log In fired');
          if(this.emailState === 2){
            axios.post(GLOBAL.domain+'/api/user/login',{email:this.email, password: this.password})
              .then(res=>{
                this.$store.dispatch('addUser', res.data.user);
                // this.$store.dispatch('urlList');
                this.$router.push('/')
              })
              .catch(err=>{
                this.$store.dispatch('notification', {header:"error", body: response.data.message || " OOps!!!! Error occur "})
              })
          }else{
            this.$store.dispatch('notification', {header: "notification", body: 'All Values must be valid'})
          }
        }
      },
      watch:{
        email(newValue, oldValue){
          if(newValue.length === 0){
            this.emailState = 0
          }
          else if( this.validateEmail(newValue)){
            this.emailState = 2
          }else{
            this.emailState = 3
          }
        },
      },
      beforeMount(){
          if(this.User) {
            this.$router.push('/');
          }
      },
      mounted(){
          this.$refs.email.focus();
      }
    }
</script>

