<template>
  <div id="app" >
    <notification></notification>
    <nav-bar class="top"></nav-bar>
    <div class="container adjust"  >
      <router-view></router-view>
    </div>
    <app-footer class="bottom"></app-footer>
  </div>
</template>

<script>
  import Navbar from '@/components/Navbar'
  import Footer from '@/components/Footer'
  import Notification from '@/components/Notification'

  export default {
    name: 'App',
    components:{
      'nav-bar' : Navbar,
      'app-footer': Footer,
      'notification': Notification
    },
    methods:{

    },
    created() {
      this.$store.dispatch('authenticateUser');
    },
    beforeUpdate(){
      this.$store.dispatch('authenticateUser');
    }
  }
</script>
<style scoped>
.adjust{
  min-height: calc(100vh - 160px);
}
</style>
<style>
  .adjust:active, .adjust:focus{
    border-color: #cccccc;
  }
  .loading{
    animation: load 3s infinite ease-in-out;
    -webkit-animation: load 3s infinite ease-in-out;
    -moz-animation: load 3s infinite ease-in-out;
  }
  @keyframes load {
    0%{
      box-shadow: 0 0 2px 1px green ;
      -moz-box-shadow: 0 0 2px 1px green ;
      -webkit-box-shadow: 0 0 2px 1px green ;
    }
    40%{
      box-shadow: 0 0 3px 1px greenyellow ;
      -moz-box-shadow: 0 0 3px 1px greenyellow ;
      -webkit-box-shadow: 0 0 3px 1px greenyellow ;
    }
    70%{
      box-shadow: 0 0 3px 1px orangered ;
      -moz-box-shadow: 0 0 3px 1px orangered ;
      -webkit-box-shadow: 0 0 3px 1px orangered ;
    }
    100%{
      box-shadow: 0 0 3px 1px crimson ;
      -moz-box-shadow: 0 0 3px 1px crimson ;
      -webkit-box-shadow: 0 0 3px 1px crimson ;
    }
  }
  .wrong{
    box-shadow: 0 0 2px 1px crimson !important;
    -webkit-box-shadow: 0 0 2px 1px crimson !important;
    -moz-box-shadow: 0 0 2px 1px crimson !important;
  }
  .wrong:focus{
    box-shadow: 0 0 2px 1px crimson !important;
    -webkit-box-shadow: 0 0 2px 1px crimson !important;
    -moz-box-shadow: 0 0 2px 1px crimson !important;
  }
  .right{
    box-shadow: 0 0 2px 1px green !important;
    -webkit-box-shadow: 0 0 2px 1px green !important;
    -moz-box-shadow: 0 0 2px 1px green !important;
  }
  .right:focus{
    box-shadow: 0 0 2px 1px green !important;
    -webkit-box-shadow: 0 0 2px 1px green !important;
    -moz-box-shadow: 0 0 2px 1px green !important;
  }
  .default-state{
    box-shadow: 0 0 2px 1px white !important;
    -webkit-box-shadow: 0 0 2px 1px white !important;
    -moz-box-shadow: 0 0 2px 1px white !important;
  }
  .default-state:focus{
    box-shadow: 0 0 0 3px #BFDEFF !important;
    -moz-box-shadow: 0 0 0 3px #BFDEFF !important;
  }
</style>



