<template>
  <div class="container">
    <div class="row">
      <div class="col-md-8 col-sm-12 col-lg-6 m-auto">
        <h1 class="display-4 text-center">Sign Up</h1>
        <p class="lead text-center">Create New Account</p>
        <form >
          <div class="form-group">
            <input ref="username" type="text" class="form-control form-control-lg adjust"
                   placeholder="Name" v-model="name">
          </div>
          <div class="form-group">
            <input type="text" class="form-control form-control-lg adjust "
                   :class="addEmailClass"
                   placeholder="Email" v-model="email">
          </div>
          <div class="form-group">
            <input type="password" class="form-control form-control-lg adjust"
                   :class="addPasswordClass"
                   placeholder="Password" v-model="password">
          </div>
          <div class="form-group">
            <input type="password" class="form-control form-control-lg adjust"
                   :class="addPasswordClass"
                   placeholder="Confirm Password" v-model="confirmPassword">
          </div>
          <div class="form-group">
            <input type="submit" @click.prevent="submit" class="btn btn-info btn-block mt-4" placeholder="Sign Up">
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
  import _ from 'lodash'
  import { mapGetters } from 'vuex'
  import axios from 'axios'
  import GLOBAL from '@/global/global'
    export default {
        name: "Signup",
      data(){
          return{
            email:"",
            password: "",
            confirmPassword: "",
            emailState:0,
            passwordState:0,
            name:""
          }
      },
      computed:{
        addEmailClass(){
          /*
          0 default
          1 loading
          2 right
          3 wrong
           */
          switch (this.emailState) {
            case 0 : return 'default-state';
            case 1 : return 'loading';
            case 2 : return 'right';
            default : return 'wrong';
          }
        },
        addPasswordClass(){
          /*
          0 default
          1 loading
          2 right
          3 wrong
           */
          switch (this.passwordState) {
            case 0 : return 'default-state';
            case 1 : return 'loading';
            case 2 : return 'right';
            default : return 'wrong';
          }
        },
        ...mapGetters(['User'])
      },
      methods:{
        validateEmail(email){
          let reg = /(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])/g
          return reg.test(email);
        },
        submit(){
          console.log('submit fired');
          if( this.emailState === 2 && this.passwordState === 2 ){
            axios.post(GLOBAL.domain+'/api/user/signup',{name: this.name, email: this.email, password: this.password})
              .then(res=>{
                console.log(res);
                this.$store.dispatch('notification',{header: "success", body:'Successfully Registered'});
                this.$router.push('/login')
              })
              .catch(err=>{
                console.log(err);
                this.$store.dispatch('notification',{header:"error", body:err.response.message});
              })
          }else{
            this.$store.dispatch('notification', {header:"warning", body: 'All Values must be valid'})
          }
        },
        checkEmail: function () {
          this.emailState = 1;
          console.log('check Email Fired');
          axios.get(GLOBAL.domain+`/api/user/checkemail`, {params: {email: this.email}})
            .then(res => {
              if (res.data.available) {
                this.emailState = res.data.available?2:3;
              }else{
                this.emailState = 3;
              }
            })
            .catch(err => {
              console.log(err.response.data);
              this.emailState = 3;
            })

        }
      },
      watch:{
        email(newValue, oldValue){
          if(newValue.length === 0){
            this.emailState = 0
          }
          else if( this.validateEmail(newValue)){
            this.debounceEmail();
          }else{
            this.emailState = 3
          }
        },
        confirmPassword(newValue, oldValue){
          if(newValue.length === 0 && this.password.length === 0 ){
              this.passwordState = 0;
          }else if( newValue === this.password ){
              this.passwordState = 2;
          }else if( newValue !==  this.password ){
            this.passwordState = 3;
          }
        },
        password(newValue, oldValue){
          if(newValue.length === 0 && this.confirmPassword.length === 0){
              this.passwordState = 0;
          }else if( newValue === this.confirmPassword){
              this.passwordState = 2;
          }else if( newValue !==  this.confirmPassword ){
            this.passwordState = 3;
          }
        }
      },
      beforeMount(){
        this.debounceEmail = _.debounce(this.checkEmail, 700);
        if(this.User){
          this.$router.push('/');
        }
      },
      mounted(){
        this.$refs.username.focus();
      }

    }
</script>
<style scoped>

</style>



