<template>
  <transition name="toast-animation">
    <div v-if="Notification.show" class="toast" :class="Notification.header">
          {{Notification.body}}
    </div>
  </transition>
</template>

<script>
  import { mapGetters }from 'vuex'
    export default {
      name: "Notification",
      computed:{
        ...mapGetters(['Notification']),
        /*
        error
        warning
        success
        notification
        default
         */
      }
    }
</script>

<style scoped>
.toast{
  min-width: fit-content;
  text-align: center;
  background-color: #333;
  color: #fff;
  border-radius: 7px;
  padding: 16px;
  position: fixed;
  z-index: 999 !important;
  left: 50%;
  bottom: 70px;
  box-shadow: 0 0 2px 0 black, -0 -0 2px 0 black;
  transform: translateX(-50%);
}
.warning{
  background-color: yellowgreen;
  box-shadow: 0 0 2px 0 yellowgreen, -0 -0 2px 0 yellowgreen;
}
.error{
  background-color: maroon;
  box-shadow: 0 0 2px 0 maroon, -0 -0 2px 0 maroon;
}
.success{
  background-color: darkgreen;
  box-shadow: 0 0 2px 0 darkgreen, -0 -0 2px 0 darkgreen;
}
.notification{
  background-color: navy;
  box-shadow: 0 0 2px 0 navy, -0 -0 2px 0 navy;
}
@keyframes move-in {
  0%{
    left: 100%
  }
  50%{
    left: 31%
  }
  100%{
    left: 50%
  }
}
@keyframes move-out {
  0%{
    left: 50%;
    transform: translateX(-50%);
  }
  50%{
    left: 69%;
  }
  100%{
    left: 0%;
  }
}
  .toast-animation-enter-active{
    animation: ease-out move-in .5s;
  }
  .toast-animation-leave-active{
    animation: ease-out move-out .5s ;
  }
  .toast-animation-enter{
    left: 100%;
  }
  .toast-animation-enter, .toast-animation-leave{
    transition: left .5s;
  }
  .toast-animation-leave-to{
    left: 0;
    transform: translateX(-100%);
  }
</style>



